import Storage from 'good-storage'


export const LAND_MAP = {
    name: "陆地图",
    icon: require('../../static/icon/map_Land.png'),
    status: true
}

export const CHANNEL_MAP = {
    name: "航道图",
    icon: require('../../static/icon/map_channel.png'),
    status: true
}

export function getMaps(key) {
    return Storage.get(key, true)
}

export function setMaps(key, status) {
    Storage.set(key, status)

    return status
}

export function resetMaps(key, status = true) {
    Storage.set(key, status)

    return status
}



