<template>
    <div class="m-switch" :class="[{small: small, round: round}, color]">
        <div class="m-switch-item" v-for="item in items"
             :class="{active: value === item.value}"
             :style="{width: 100 / items.length + '%'}"
             @click="handlerItem($event, item)">
            {{item.name}}
        </div>
        <div class="m-switch-bar" :style="{left: barLeft, width: 100 / items.length + '%'}"></div>
    </div>
</template>

<script>
    export default {
        name: "m-switch",
        props: {
            value: {},
            items: {
                type: Array,
                default: () => []
            },
            small: {
                type: Boolean,
                default: false
            },
            round: {
                type: Boolean,
                default: false
            },
            color: {
                type: String,
                default: ''
            }
        },
        computed: {
            barLeft() {
                const index = this.items.findIndex(item => {
                    return item.value === this.value
                })

                return (100 / this.items.length) * index + '%'
            },
        },
        methods: {
            handlerItem(event, item) {
                if (item.value !== this.value) {
                    this.$emit('change', item.value)
                }

                this.$emit('input', item.value)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .m-switch
        position relative
        display flex
        align-items center
        justify-content center
        width 100%
        min-width 80px
        height 30px
        border-radius 5px
        overflow hidden

        .m-switch-item
            position relative
            z-index 100
            display flex
            align-items center
            justify-content center
            height 100%
            font-size 12px
            border-radius 2px
            transition all .5s

            &.active
                color #fff


        .m-switch-bar
            position absolute
            top 0
            height 30px
            color #fff
            transition all .5s

        &.round
            border-radius 100px

        &.small
            height 24px

            .m-switch-item
                width 42px
</style>



