import Storage from 'good-storage'
import {changeArrayItem} from "../common/utils/history"

// keys
const LAYERS_KEY = '__GDHDT_LAYER__'

// default
export const LAYERS = [
    {
        "name": "水位站",
        "id": "013010",
        "status": false,
        icon: require('../../static/icon/layer_water.png'),
        color: '#31C7D0'
    },
    {
        "name": "船舶",
        "id": "013001",
        "status": false,
        icon: require('../../static/icon/layer_boat.png'),
        color: '#F07373'
    },
    {
        "name": "航标",
        "id": "013007",
        "status": true,
        icon: require('../../static/icon/layer_navigation.png'),
        color: '#4D7BCC'
    },
    {
        "name": "天气",
        "id": "013011",
        "status": false,
        icon: require('../../static/icon/layer_weather.png'),
        color: '#ff9800'
    },
    {
        "name": "航道",
        "id": "013006",
        "status": false,
        icon: require('../../static/icon/layer_river.png'),
        color: '#72BD3E'
    },
    {
        "name": "标注",
        "id": "001001",
        "status": false,
        icon: require('../../static/icon/layer_label.png'),
        color: '#3899D7'
    }
]

export function getLayers() {
    const result = Storage.get(LAYERS_KEY, false)

    if (!result) {
        Storage.set(LAYERS_KEY, LAYERS)

        return LAYERS
    }

    return result
}

export function setLayers(layers) {

    Storage.set(LAYERS_KEY, layers)

    return layers
}

export function saveLayers(target) {
    let searches = getLayers()

    changeArrayItem(searches, target, item => {
        return item.id === target.id
    })

    Storage.set(LAYERS_KEY, searches)

    return searches
}

export function clearLayers() {

    Storage.set(LAYERS_KEY, LAYERS)

    return LAYERS
}



