import {getValidate, saveValidate, deleteValidate} from "../storage/validate"
import * as map from './map'
import * as app from './app'
import * as theme from './theme'
import dispense from './event'

export const production = process.env.NODE_ENV === 'production'

export const isObject = obj => Object.prototype.toString.call(obj) === '[object Object]'

export const isArray = arr => Array.isArray(arr) ? arr : typeof arr === 'undefined' || arr === null ? [] : [arr]

export const isFalser = value => typeof value === 'undefined' || value === null || value === ''

export function debounce(func, delay) {
    let timer

    return function (...args) {
        if (timer) {
            clearTimeout(timer)
        }
        timer = setTimeout(() => {
            func.apply(this, args)
        }, delay)
    }
}

export function extend() {
    let options, name, src, copy, copyIsArray, clone, target = arguments[0] || {},

        i = 1,
        length = arguments.length,
        deep = false

    if (typeof target === "boolean") {
        deep = target

        target = arguments[i] || {}
        i++
    }

    if (typeof target !== "object" && typeof target !== 'function') {
        target = {}
    }

    if (i === length) {
        target = this
        i--
    }

    for (; i < length; i++) {

        if ((options = arguments[i]) != null) {
            for (name in options) {
                src = target[name]
                copy = options[name]

                if (target === copy) {
                    continue
                }

                if (deep && copy && (copy.constructor.prototype === Object.prototype || (copyIsArray = Array.isArray(copy)))) {
                    // 深复制
                    if (copyIsArray) {

                        copyIsArray = false

                        clone = src && Array.isArray(src) ? src : []
                    } else {
                        clone = src && src.constructor.prototype === Object.prototype ? src : {}
                    }

                    target[name] = extend(deep, clone, copy);

                } else if (copy !== undefined) {
                    target[name] = copy
                }
            }
        }
    }

    return target
}

export function imageToBase64(url) {
    return new Promise((resolve, reject) => {
        const img = new Image()

        img.crossOrigin = 'Anonymous'

        img.onload = function () {
            const canvas = document.createElement('canvas')
            const ctx = canvas.getContext('2d')

            canvas.height = this.height
            canvas.width = this.width

            ctx.drawImage(this, 0, 0)

            const dataURL = canvas.toDataURL()

            resolve(dataURL)
        }

        img.onerror = reject

        img.src = url
    })
}

export function blobToBase64(blob) {
    const reader = new FileReader()

    return new Promise((resolve, reject) => {
        reader.onload = event => {
            resolve(event.target.result)
        }

        reader.onerror = reject

        reader.readAsDataURL(blob)
    })
}

export function validate(target, firstValidate, step = 180) {
    let past

    // 查询之前是否存储过验证码
    past = getValidate(target.phone, target.type)

    // 如果没有存储，则设置时间
    if (!past.time && !firstValidate) {
        target.time = Date.now()
        target.disabled = true
        past = saveValidate(target)
        intervalTimer()
        target.timer = setInterval(intervalTimer, 1000)

        return
    } else if (!past.time) {
        clearInterval(target.timer)

        target.disabled = false

        target.text = '获取验证码'

        return
    }

    if (Date.now() - past.time > step * 1000) {
        target.disabled = false
        target.text = '获取验证码'
        deleteValidate(target)
        clearInterval(target.timer)
    } else {
        target.disabled = true

        intervalTimer()

        target.timer = setInterval(intervalTimer, 1000)
    }

    function intervalTimer() {
        const time = Math.round((past.time - new Date()) / 1000) + step

        target.text = `请等待（${time}s）`

        if (time < 1) {
            target.disabled = false
            target.text = '获取验证码'
            deleteValidate(target)
            clearInterval(target.timer)
        }
    }
}

export function versionContrast(c = '0.0.0', o = '0.0.0') {
    c = c.split('.')
    o = o.split('.')

    for (let i = 0; i < 3; i++) {
        if (c[i] > o[i]) return false

        if (c[i] < o[i]) {
            console.log(`APP Version: 最新版本是 ${o.join('.')}`)
            return true
        }
    }
    return false
}

export default {
    production, map, app, theme, dispense
}



