import {
    getLayers,
    setLayers,
    saveLayers,
    clearLayers
} from "../../storage/layer"
import {changeMapFramesServerState, showLayers, hideLayers} from "../../common/js/cordova/map"


const SET_LAYERS = 'SET_LAYERS'


export default {
    namespaced: true,
    state: {
        layers: getLayers()
    },
    getters: {
        layers: state => state.layers
    },
    mutations: {
        [SET_LAYERS](state, layers) {
            state.layers = layers
        }
    },
    actions: {
        setLayers({commit}, layers) {
            commit(SET_LAYERS, setLayers(layers))
        },
        async saveLayers({commit, state}, target) {
            // 如果是图幅，则调用图幅专属方法开关，如果不是根据状态调用开关
            if (target.id === '013006') {
                await changeMapFramesServerState(target.status)
            }
            else if (target.status) {
                await showLayers([target.id])
            } else {
                await hideLayers([target.id])
            }

            commit(SET_LAYERS, saveLayers(target))

            console.log(`%c更改${target.name}（${target.id}）状态: ${target.status}`, 'color: #e65100')
        },
        resetLayers({commit}) {
            commit(SET_LAYERS, clearLayers())
        }
    }
}



