import app from './app'
import element from './element'
import layer from './layer'
import map from './map'
import route from './route'
import search from './search'
import user from './user'
import notice from './notice'
import navigational from './navigational'

export const appMixins = app

export const elementMixins = element

export const layerMixins = layer

export const mapMixins = map

export const routeMixins = route

export const searchMixins = search

export const userMixins = user

export const noticeMixins = notice

export const navigationalMixins = navigational

export default {
    appMixins,
    elementMixins,
    layerMixins,
    mapMixins,
    routeMixins,
    searchMixins,
    userMixins,
    noticeMixins,
    navigationalMixins
}



