import Router from "../router/index"
import Store from "../store/index"
import {getDepth, getMapBoat, getMapLabel, getMapMark, getMapWater, getMapWeather, getRouteTips,getReport} from "../api/map"
import {isArray} from "./index"

// 图层点面板
export const element = {}

// 指南针
export const compass = {}

// 比例尺
export const scale = {}

// 导航
export const navigation = {
    getClearanceHeight(name, x, y, types) {
        getRouteTips({
            centerPoint: {
                longitude: x,
                latitude: y,
            },
            name,
            types: isArray(types)
        }).then(res => {
            window.Arcgis && window.Arcgis.setTargetInfo(JSON.stringify(res))
        }).catch(err => {
            console.error(`净空高度请求发生错误: `, err)
        })
    },
    getWaterLevelStation(name, x, y, types) {
        getRouteTips({
            centerPoint: {
                longitude: x,
                latitude: y,
            },
            name,
            types: isArray(types)
        }).then(res => {
            window.Arcgis && window.Arcgis.getGaugingStation(JSON.stringify(res))
        }).catch(err => {
            console.error(`水位站请求发生错误: `, err)
        })
    }
}

