<template>
    <div id="app">
        <transition name="fade">
            <keep-alive>
                <router-view/>
            </keep-alive>
        </transition>

        <popup class="drawer" position="right" width="210px"
               :lazy-render="false" :value="drawer"
               @click-overlay="setDrawer(false)"
               right>
            <div @touchmove.stop class="scroll-wrapper">
                <mu-list dense>
                    <mu-sub-header>基础地图</mu-sub-header>
                    <div class="map-mode">
                        <div :class="{active: channelMap}" @click="toggleChannelMap(!channelMap)"
                             class="map-mode-item tc">
                            <div class="map-mode-icon w border-1px">
                                <img :src="CHANNEL_MAP.icon" class="db w h">
                            </div>
                            <span class="lhp fz-14">{{CHANNEL_MAP.name}}</span>
                        </div>
                        <div :class="{active: landMap}" @click="toggleLandMap(!landMap)"
                             class="map-mode-item tc">
                            <div class="map-mode-icon w border-1px">
                                <img :src="LAND_MAP.icon" class="db w h">
                            </div>
                            <span class="lhp fz-14">{{LAND_MAP.name}}</span>
                        </div>
                    </div>
                    <mu-sub-header>专题图</mu-sub-header>
                    <div class="map-layers w">
                        <div @click="toggleLayer(item, index)" class="map-layers-item flex aic w33"
                             v-for="(item, index) in layers">
                            <mu-avatar :color="item.status ? item.color : '#D9D9D9'" size="45">
                                <img :src="item.icon" class="db p-10">
                            </mu-avatar>
                            <span class="theme-text-secondary fz-14 lhp">{{item.name}}</span>
                        </div>
                    </div>
                    <mu-sub-header>导航模式</mu-sub-header>
                    <mu-list-item>
                        <mu-list-item-action>
                            <img :src="themeIcon" class="icon navigation-icon">
                        </mu-list-item-action>
                        <mu-list-item-title>模式切换</mu-list-item-title>
                        <mu-list-item-action>
                            <m-swatch :items="[THEME_LIGHT, THEME_NIGHT]" :value="theme" @change="toggleTheme"
                                      color="green"
                                      round small/>
                        </mu-list-item-action>
                    </mu-list-item>
                    <mu-list-item>
                        <mu-list-item-action>
                            <img :src="angleIcon" class="icon navigation-icon direction">
                        </mu-list-item-action>
                        <mu-list-item-title>导航视角</mu-list-item-title>
                        <mu-list-item-action>
                            <m-swatch :items="[ANGLE_NORTH, ANGLE_HEAD]" :value="angle" @change="toggleAngle"
                                      color="green"
                                      round small/>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
            </div>
        </popup>

        <mu-dialog :esc-press-close="false" :open.sync="news.visible" :overlay-close="false"
                   :scrollable="true"
                   lock-scroll
                   max-width="90%"
                   title="发现新版本"
                   width="100%">
            <div class="flex fc aic">
                <div class="news-icon-wrapper mb-10 mu-primary-color">
                    <mu-icon color="#fff" size="70" value="arrow_upward"></mu-icon>
                </div>
                <div class="lht theme-text-secondary">{{version}}</div>
                <ul class="w">
                    <li class="lhp">版本：{{news.version}}</li>
                    <li class="lhp">大小：{{news.file.size}}M</li>
                    <li class="lhp">更新时间：{{news.uploadTime}}</li>
                    <li class="lhp">更新内容： {{news.uploadContent}}</li>
                </ul>
            </div>

            <!-- dialog -->
            <mu-dialog :esc-press-close="false" :open.sync="progress.visible"
                       :overlay-close="false"
                       lock-scroll
                       width="80%">
                <mu-sub-header>{{tips.download}}</mu-sub-header>
                <br/>
                <m-progress :max="progress.max" :progress="progress.value"/>
                <br/>
            </mu-dialog>
            <!-- dialog -->
            <mu-button @click="closeUpload" flat slot="actions" v-if="!news.compulsively">取消</mu-button>
            <mu-button @click="downloadApk" color="primary" flat slot="actions">更新</mu-button>
        </mu-dialog>
    </div>
</template>

<script>
    import Vue from 'vue'
    import MSwatch from './base/switch/switch'
    import Popup from 'vant/lib/popup'
    import MProgress from './base/progress/progress'
    import 'vant/lib/popup/style'
    import {
        changeChartLayerVisible,
        changeMapFramesServerState,
        changeTheme,
        changeTieldLayerVisible,
        hideLayers,
        installationApkfile,
        setMapCompassMode,
        setMapEnable,
        setMapTruenorth,
        showLayers
    } from './common/js/cordova/map'
    import {appMixins, layerMixins, mapMixins, navigationalMixins, routeMixins, searchMixins} from "./common/mixins"
    import {getParams} from "./api/search"
    import {checkUpdate, getSetters} from "./api/sys"
    import {downloadFile, getSavePath} from "./common/js/cordova/download"
    import {setResources} from "./storage/app"
    import {app} from "./common/js/cordova"
    import {versionContrast} from "./types"
    import Theme from 'muse-ui/lib/theme'
    import Toast from 'muse-ui-toast'

    import {tips} from "./types/tips"

    let BACK_TIMER, BACK_COUNTER = 0

    export default {
        name: 'App',
        mixins: [layerMixins, mapMixins, appMixins, searchMixins, navigationalMixins, routeMixins],
        data() {
            return {
                news: {
                    visible: false,
                    version: '0.0.0',
                    file: {
                        size: 0,
                        url: '',
                        savePath: ''
                    },
                    uploadTime: '',
                    uploadContent: '',
                    compulsively: false
                },
                progress: {
                    visible: false,
                    value: 0,
                    max: 100
                },
                tips: {
                    download: tips.app.download
                }
            }
        },
        created() {
            getParams('imgPrefixPath').then(res => {
                setResources(res)

                Vue.prototype.$images = `${res}`

                console.log(`%c图片资源服务器地址： ${res}`, 'color: #66ccff')
            })


            document.addEventListener("deviceready", this._initCordova, false)

            if (process.env.NODE_ENV === 'development') {
                this._initCordova()
            }
        },
        mounted() {
            const width = window.innerWidth
            const height = window.innerHeight
            const html = window.document.documentElement

            html.style.width = `${width}px`
            html.style.height = `${height}px`

            this.setScreen({width, height})
        },
        methods: {
            // TODO: Android&IOS Cordova init function.
            _initCordova() {
                console.log("Cordova load successfully!")

                if (window.cordova
                    && window.cordova.platformId == 'android'
                    && parseFloat(window.cordova.platformVersion) >= 6) {
                    console.log("Android 6.0 以上，获取权限 ...")

                    const permissions = window.cordova.plugins.permissions

                    const list = [
                        permissions.WRITE_EXTERNAL_STORAGE,
                        permissions.READ_EXTERNAL_STORAGE,
                        permissions.ACCESS_COARSE_LOCATION,
                        permissions.ACCESS_FINE_LOCATION,
                        permissions.CALL_PHONE
                    ]

                    permissions.checkPermission(list, function (status) {
                        if (!status.hasPermission) {
                            //没有则请求权限
                            permissions.requestPermissions(list, function (status2) {
                                if (status2.hasPermission) {
                                    //Arcgis.reload();
                                    //location.reload();
                                }
                            }, null)
                        } else {
                            console.warn("permissions all has.")
                        }
                    }, null)
                }

                // window.map = window.Arcgis
                //
                // window.map.positionFeatureByParams = function (x, y, options, callback) {
                //     window.map.centerAt(options.x, options.y)
                //
                //     callback && callback()
                // }

                // TODO: Listener Back button.
                document.addEventListener('backbutton', this._initBackButton, false)

                // TODO: APP init.
                this._initAppDefault()
            },
            // TODO: Android back button click function.
            _initBackButton() {
                // 在首页时点击返回键
                if (this.$route.path === '/') {
                    // 两秒内首次点击: 提示是否继续点击
                    if (BACK_COUNTER === 0) {
                        clearTimeout(BACK_TIMER)

                        BACK_COUNTER++

                        this.$toast({
                            message: '再按一次退出航道通！',
                            duration: 2000
                        })

                        BACK_TIMER = setTimeout(() => {
                            BACK_COUNTER = 0
                        }, 1900)

                        return
                    }

                    // 两秒内二次点击: 退出APP
                    if (BACK_COUNTER === 1) {
                        navigator.app && navigator.app.exitApp()
                        return
                    }
                    return
                }

                if (this.$route.name === 'sail' || this.$route.name === 'navigation') {
                    window.Arcgis && window.Arcgis.cancelPathPlanning('')

                    this.resetRoutes()
                }

                if (this.$route.name === 'sail-over') {
                    window.Arcgis && window.Arcgis.cancelPathPlanning('')
                    window.Arcgis && window.Arcgis.clearMask()

                    this.resetRoutes()

                    this.$router.replace('/')
                }

                // 其他时候点击返回键
                this.$router.back()
            },
            // TODO: APP types&const&global variable init.
            _initAppDefault() {
                const html = window.document.documentElement

                /***
                 * 初始化
                 */
                html.addEventListener('touchstart', this.touchStart)
                html.addEventListener('touchend', this.touchEnd)

                document.addEventListener("jpush.openNotification", this.onOpenNotification, false)
                document.addEventListener("jpush.receiveNotification", this.onReceiveNotification, false)
                document.addEventListener("jpush.receiveMessage", this.onReceiveMessage, false)

                window.addEventListener('resize', () => {
                    html.style.width = `${window.innerWidth}px`
                    html.style.height = `${window.innerHeight}px`

                    this.setScreen({width: window.innerWidth, height: window.innerHeight})
                })

                app.version().then(version => {
                    this.setVersion(version)

                    checkUpdate(version).then(res => {
                        if (res && res.version && versionContrast(this.version, res.version)) {
                            this.news.visible = true
                            this.news.version = res.version
                            this.news.file.size = (res.file_size / 1024 / 1024).toFixed(2)
                            this.news.file.url = res.file_url
                            this.news.uploadTime = res.upload_time
                            this.news.uploadContent = res.detail
                            this.news.compulsively = res.compulsively
                        } else {
                            console.log(`APP Version: ${version}, 已经是最新版本`)
                        }
                    })
                })

                if (this.brightness >= 0) {
                    this.setBrightness(this.brightness)
                }


                // 是否打开 GPS & 网络
                window.Arcgis && window.Arcgis.registerNetReceiver()
                window.Arcgis && window.Arcgis.observeWifiSwitch()
                window.Arcgis && window.Arcgis.isopengps(visible => {
                    this.setGPS(visible)
                })

                // 路地图
                changeTieldLayerVisible(this.landMap)
                // 电子航道图
                changeChartLayerVisible(this.channelMap)

                // 图层点
                const layers = this.layers.map((item, index) => {
                    if (item.id === "013006") {
                        changeMapFramesServerState(item.status)
                    } else if (item.status) {
                        showLayers([item.id])
                    } else {
                        hideLayers([item.id])
                    }

                    return Object.assign({}, item, {
                        icon: this.LAYERS[index].icon,
                        id: this.LAYERS[index].id,
                        name: this.LAYERS[index].name,
                        color: this.LAYERS[index].color
                    })
                })

                this.setLayers(layers)

                // 主题
                changeTheme(this.theme)

                Theme.use(this.theme)

                window.$theme = Theme

                console.log(`%c载入主题 ${this.theme}`, 'color: blue')

                this.setModeTiming()

                // 搜索结果图标
                getSetters({key: 'SEARCH_RESULT_ICO'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.saveSearchIcons(res)
                })

                // 搜索类型图标
                getSetters({key: 'SEARCH_TYPE_ICO'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.setIcons(res)
                })

                // 搜索类型列表
                getSetters({key: 'SEARCH_TYPE_LIST'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.saveSearchList(res)
                })

                // 搜索一键呼叫
                getSetters({key: 'EMERGENCY_CONTACT'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.setPhones(res)
                })
            },
            // TODO: APP Alert
            onOpenNotification(event) {
                console.log("onOpenNotification")
                this.$router.push({name: 'about-message'})
                console.log(event.alert)
            },
            onReceiveNotification(event) {
                console.log("onReceiveNotification")
                console.log(event.alert)
            },
            onReceiveMessage(event) {
                console.log("onReceiveMessage")
                console.log(event.message)
            },
            // TODO: prohibit user touch Maps.
            touchStart(e) {
                const enable = !!e.target.getAttribute('data-enable')
                const color = enable ? '#009688' : '#f44336'

                console.log(`%c—————— Enable start ——————`, `color: ${color}`)

                setMapEnable(enable)
            },
            touchEnd(e) {
                const enable = e.target.getAttribute('data-enable') ? '#009688' : '#f44336'

                console.log(`%c—————— Enable end ——————`, `color: ${enable}`)


                setMapEnable(true)
            },
            // TODO: layers.
            // 切换陆地图状态
            toggleLandMap(visible) {
                changeTieldLayerVisible(visible).then(res => {
                    this.setLandMap(visible)

                    console.log(`%c 切换路地图 ————  Visible: ${visible}`, 'color: #111')
                })
            },
            // 切换航道图
            toggleChannelMap(visible) {
                changeChartLayerVisible(visible).then(res => {
                    this.setChannelMap(visible)

                    console.log(`%c 切换航道图 ————  Visible: ${visible}`, 'color: #111')
                })
            },
            // 切换要素点显示隐藏
            toggleLayer(item, index) {
                if (index === 1 && !item.status) {
                    Toast.message({
                        position: 'bottom',
                        message: '船舶动态数据可能有延时，仅供参考',
                        time: 5000
                    })
                }

                if (index === 3 && !item.status) {
                    Toast.message({
                        position: 'bottom',
                        message: '天气信息来源于中国天气网，仅供参考',
                        time: 5000
                    })
                }

                item = Object.assign({}, item, {status: !item.status})

                this.saveLayers(item)
            },
            /***
             * 改变地图主题
             * @param theme
             */
            toggleTheme(theme) {
                this.setTheme(theme)

                console.log(`%c当前主题为 ${theme}`, 'color: #673ab7')
            },
            /***
             * 改变视角
             * @param value
             */
            toggleAngle(value) {
                if (value === this.ANGLE_NORTH.value) {
                    setMapTruenorth().then(res => {
                        this.setAngle(value)

                        console.log(`%c当前视角为 ${value}`, 'color: #673ab7')
                    })
                }
                if (value === this.ANGLE_HEAD.value) {
                    setMapCompassMode().then(res => {
                        this.setAngle(value)

                        console.log(`%c当前视角为 ${value}`, 'color: #673ab7')
                    })
                }
            },
            // TODO: Download APK.
            downloadApk() {
                this.progress.visible = true
                // this.news.file.url = this.$images + this.news.file.url
                if (!this.news.file.url) {
                    return this.$toast('无效下载地址')
                }

                console.log(`%c新版本下载地址: ${this.news.file.url}`, 'color: #9c27b0')

                this.news.file.savePath = getSavePath() + this.version + ".apk"

                downloadFile(this.news.file.url, this.news.file.savePath, success => {
                    let data = JSON.parse(success.toString())

                    //下载完成安装apk
                    if (data.type === "completed") {
                        this.progress.visible = false
                        this.news.visible = false

                        installationApkfile(this.news.file.savePath)
                    }

                    if (data.type === "progress") {
                        this.progress.value = Number.parseInt(data.soFarBytes)
                        this.progress.max = Number.parseInt(data.totalBytes)
                    }
                }, console.log)
            },
            closeUpload() {
                this.news.visible = false
            }
        },
        watch: {
            $route(to, form) {
                setMapEnable(true)
            },
            theme(value) {
                Theme.use(value)

                console.log(`%c载入主题 ${this.theme}`, 'color: blue')
            }
        },
        components: {
            Popup,
            MSwatch,
            MProgress
        }
    }
</script>

<style lang="stylus">
    [v-cloak] {
        display none !important !important
    }
</style>

<style lang="stylus">
    @import "./common/stylus/variable.styl"
    @import "./common/stylus/mixins.styl"

    #app
        position relative
        width 100%
        height 100%
        overflow hidden

    #app
        background-color transparent

    .van-image-preview__overlay
        z-index 10000 !important

    .van-image-preview
        z-index 10001 !important

    .mu-load-more
        width 100%
        min-height 100%

    .more-popover
        left 0 !important
        right 0
        max-width 100%


    .news-icon-wrapper
        width 70px
        height 70px
        border-radius 50%
        overflow hidden


    .drawer
        width 210px
        height 100vh
        z-index: 20141200 !important;
        overflow hidden

        .map-mode
            display flex
            padding 0 10px

            .map-mode-item
                flex 1
                padding 0 5px

                .map-mode-icon
                    position relative
                    margin-bottom 5px

                &.active
                    .map-mode-icon
                        border-color #2196f3

                    .map-mode-icon:after
                        content 'done'
                        position absolute
                        left 0
                        bottom 0
                        width 100%
                        height 18px
                        font-family 'Material Icons'
                        text-align center
                        color #fff
                        background-color #2196f3

        .mu-item-action
            min-width 32px

        .icon
            display block
            padding 3px
            width 22px
            height 22px
            border-radius 50%

        .map-layers
            display flex
            flex-wrap wrap

            .map-layers-item
                flex-direction column
                padding 7px 0

        .navigation-icon
            background-color #ff5252

        .navigation-icon.direction
            background-color #2196f3

        .depth-icon
            background-color #8BBE4E

            &.focus
                background-color #d9d9d9

</style>



