import {mapGetters, mapActions} from 'vuex'

export default {
    computed: {
        ...mapGetters({
            navigationalIcons: 'navigational/icons',
        })
    },
    methods: {
        ...mapActions({
            setNavigationalIcons: 'navigational/setNavigationalIcons',
            clearNavigationalIcons: 'navigational/clearNavigationalIcons'
        })
    }
}



