import Vue from 'vue'
import Router from 'vue-router'
import Store from '../store'
import Toast from 'vant/lib/toast'

let timer = null

Vue.use(Router)

const router = new Router({
    // mode: 'history',
    routes: [
        // 首页
        {
            path: '/',
            name: 'map',
            component: h => import('../views/index'),
            children: [
                // 地图选点-编辑
                {
                    path: 'selection/point',
                    name: 'selection-point',
                    component: h => import('../views/about/tagging/editor'),
                    props: true
                },
                // 地图上报-编辑
                {
                    path: 'selection/report',
                    name: 'selection-report',
                    component: h => import('../views/about/tagging/editor'),
                    props: true
                },
                // 新闻资讯
                {
                    path: 'news',
                    name: 'news',
                    component: h => import('../views/news/index'),
                    children: [
                        {
                            path: ':id',
                            name: 'news-detail',
                            component: h => import('../views/news/details'),
                            props: true
                        }
                    ]
                },
                // 动态圈
                {
                    path: 'dynamic',
                    name: 'dynamic',
                    component: h => import('../views/dynamic/index'),
                    meta: {
                        login: true
                    },
                    children: [
                        {
                            path: 'type',
                            name: 'dynamic-type-list',
                            component: h => import('../views/dynamic/type-list'),
                            meta: {
                                login: true
                            },
                            children: [
                                {
                                    path: 'issue',
                                    name: 'dynamic-issue',
                                    component: h => import('../views/dynamic/issue'),
                                    meta: {
                                        login: true
                                    }
                                },
                                {
                                    path: 'job',
                                    name: 'dynamic-job-info',
                                    component: h => import('../views/dynamic/job-info'),
                                    meta: {
                                        login: true
                                    }
                                },
                                {
                                    path: 'recruit',
                                    name: 'dynamic-recruit-info',
                                    component: h => import('../views/dynamic/recruit-info'),
                                    meta: {
                                        login: true
                                    }
                                },
                                {
                                    path: 'supply',
                                    name: 'dynamic-supply-info',
                                    component: h => import('../views/dynamic/supply-info'),
                                    meta: {
                                        login: true
                                    }
                                },
                                {
                                    path: 'transport',
                                    name: 'dynamic-transport-info',
                                    component: h => import('../views/dynamic/transport-info'),
                                    meta: {
                                        login: true
                                    }
                                }
                            ]
                        },
                        {
                            path: ':id',
                            name: 'dynamic-detail',
                            component: h => import('../views/dynamic/detail'),
                            meta: {
                                login: true
                            },
                            props: true
                        }
                    ]
                },
                // 助航信息
                {
                    path: 'navigational',
                    name: 'navigational',
                    component: h => import('../views/navigational/index'),
                    children: [
                        {
                            path: ':type/:id',
                            name: 'navigational-details',
                            component: h => import('../views/details/detail'),
                            props: true
                        }
                    ]
                },
                // 详情
                {
                    path: 'details/:type/:id',
                    name: 'details',
                    component: h => import('../views/details/detail'),
                    props: true
                },
                // 个人中心
                {
                    path: 'about',
                    name: 'about',
                    component: h => import('../views/about/index'),
                    children: [
                        // 登录&注册&忘记密码
                        {
                            path: 'login',
                            name: 'login',
                            component: h => import('../views/user/login'),
                            children: [
                                // 注册
                                {
                                    path: '/register',
                                    name: 'register',
                                    component: h => import('../views/user/register')
                                },
                                // 忘记密码
                                {
                                    path: '/forget',
                                    name: 'forget-password',
                                    component: h => import('../views/user/forget')
                                }
                            ]
                        },
                        {
                            path: 'ship',
                            name: 'about-ship',
                            component: h => import('../views/about/ship/index'),
                            meta: {
                                login: true
                            },
                            children: [
                                {
                                    path: ':type/:id',
                                    name: 'about-ship-detail',
                                    meta: {
                                        login: true
                                    },
                                    component: h => import('../views/details/detail'),
                                    props: true
                                }
                            ]
                        },
                        {
                            path: 'message',
                            name: 'about-message',
                            component: h => import('../views/about/message/index'),
                            children: [
                                {
                                    path: 'notice/:id',
                                    name: 'about-message-detail',
                                    component: h => import('../views/about/message/detail'),
                                    props: true
                                },
                                {
                                    path: 'news/:id',
                                    name: 'about-message-chat',
                                    component: h => import('../views/about/message/chat'),
                                    props: true
                                }
                            ]
                        },
                        {
                            path: 'data',
                            name: 'about-data',
                            component: h => import('../views/about/data/index'),
                            meta: {
                                login: true
                            },
                            children: [
                                {
                                    path: 'ship',
                                    name: 'about-data-ship',
                                    component: h => import('../views/about/data/add-ship'),
                                    meta: {
                                        login: true
                                    },
                                },
                                {
                                    path: 'phone',
                                    name: 'about-data-phone',
                                    component: h => import('../views/about/data/phone'),
                                    meta: {
                                        login: true
                                    },
                                },
                                {
                                    path: 'info',
                                    name: 'about-data-info',
                                    component: h => import('../views/about/data/info'),
                                    meta: {
                                        login: true
                                    },
                                },
                                {
                                    path: 'password',
                                    name: 'about-data-password',
                                    component: h => import('../views/about/data/password'),
                                    meta: {
                                        login: true
                                    },
                                }
                            ]
                        },
                        {
                            path: 'tagging',
                            name: 'about-tagging',
                            component: h => import('../views/about/tagging/index'),
                            meta: {
                                login: true
                            },
                            children: [
                                {
                                    path: ':id',
                                    name: 'about-tagging-editor',
                                    component: h => import('../views/about/tagging/editor'),
                                    props: true,
                                    meta: {
                                        login: true
                                    }
                                }
                            ]
                        },
                        {
                            path: 'address',
                            name: 'about-common-address',
                            component: h => import('../views/about/address/index'),
                            meta: {
                                login: true
                            },
                            children: [
                                {
                                    path: 'save',
                                    name: 'about-common-address-editor',
                                    component: h => import('../views/about/address/editor'),
                                    meta: {
                                        login: true
                                    }
                                }
                            ]
                        },
                        {
                            path: 'remind',
                            name: 'about-remind',
                            component: h => import('../views/about/remind')
                        },
                        {
                            path: 'setter',
                            name: 'about-setter',
                            component: h => import('../views/about/setter/index'),
                            children: [
                                {
                                    path: 'brightness',
                                    name: 'about-setter-brightness',
                                    component: h => import('../views/about/setter/brightness')
                                },
                                {
                                    path: 'mode',
                                    name: 'about-setter-mode',
                                    component: h => import('../views/about/setter/mode')
                                },
                                {
                                    path: 'notice',
                                    name: 'about-setter-notice',
                                    component: h => import('../views/about/setter/notice')
                                },
                                {
                                    path: 'update',
                                    name: 'about-setter-update',
                                    component: h => import('../views/about/setter/update')
                                },
                                {
                                    path: 'help',
                                    name: 'about-setter-help',
                                    component: h => import('../views/about/setter/help')
                                },
                                {
                                    path: 'related',
                                    name: 'about-setter-related',
                                    component: h => import('../views/about/setter/related')
                                }
                            ]
                        },
                        {
                            path: 'feedback',
                            name: 'about-feedback',
                            component: h => import('../views/about/feedback/feedback')
                        },
                        {
                            path: 'offline-map',
                            name: 'offline-map',
                            component: h => import('../views/about/offline-map'),
                            meta: {
                                // block: true
                            }
                        },
                        {
                            path: 'report',
                            name: 'about-report',
                            component: h => import('../views/about/report/index'),
                            meta: {
                                login: true
                            },
                            children: [
                                {
                                    path: ':type/:id',
                                    name: 'about-report-detail',
                                    meta: {
                                        login: true
                                    },
                                    component: h => import('../views/about/report/editor'),
                                    props: true
                                }
                            ]
                        },
                    ]
                },
                // 意见反馈
                {
                    path: 'feedback',
                    name: 'feedback',
                    component: h => import('../views/about/feedback/feedback')
                }
            ]
        },
        {
            path: '/search',
            name: 'search',
            component: h => import('../views/search/index'),
            children: [
                {
                    path: ':type/:id',
                    name: 'search-detail',
                    component: h => import('../views/details/detail'),
                    props: true
                }
            ]
        },
        {
            path: '/message',
            name: 'message',
            component: h => import('../views/about/message/index'),
            children: [
                {
                    path: 'notice/:id',
                    name: 'message-notice',
                    component: h => import('../views/about/message/detail'),
                    props: true
                },
                {
                    path: 'news/:id',
                    name: 'message-news',
                    component: h => import('../views/about/message/chat'),
                    props: true
                }
            ]
        },
        {
            path: '/navigation',
            name: 'navigation',
            component: h => import('../views/navigation/index'),
            props: true,
            children: [
                {
                    path: 'sn',
                    name: 'route-sn',
                    component: h => import('../views/navigation/search')
                },
                {
                    path: 'en',
                    name: 'route-en',
                    component: h => import('../views/navigation/search')
                }
            ]
        },
        {
            path: '/selection/sn',
            name: 'selection-sn',
            component: h => import('../views/selection/index')
        },
        {
            path: '/selection/en',
            name: 'selection-en',
            component: h => import('../views/selection/index')
        },
        {
            path: '/selection/cl',
            name: 'selection-cl',
            component: h => import('../views/selection/index')
        },
        {
            path: '/sail',
            name: 'sail',
            component: h => import('../views/sail/index')
        },
        {
            path: '/over',
            name: 'sail-over',
            component: h => import('../views/sail/over')
        }
    ]
})

router.beforeEach((to, form, next) => {
    console.log(`%c—————— form: ${form.name}; to: ${to.name}; login: ${to.meta.login} ——————`, 'color: #66ccff')

    clearTimeout(timer)
    timer = setTimeout(() => {
        Toast.clear()
    }, 2000)

    if (to.meta.block || typeof to.name === 'undefined' || to.name === null) {
        next({name: form.name})
        return
    }

    if (to.meta.login && !Store.getters['user/isLogin']) {
        next({name: 'login'})
        return
    }

    next()
})

export default router



