import {getMaps, setMaps, resetMaps} from "../../storage/map"

// keys
const LAND_MAP_KEY = '__GDHDT_LAND_MAP__'
const CHANNEL_MAP_KEY = '__GDHDT_CHANNEL_MAP__'

// const
const SET_LAND_MAP = 'SET_LAND_MAP'
const SET_CHANNEL_MAP = 'SET_CHANNEL_MAP'
const SET_ANGLE = 'SET_ANGLE'
const SET_DEPTH = 'SET_DEPTH'

// default
export const ANGLE_NORTH = {
    name: '正北',
    value: '正北',
    icon: require('../../../static/icon/map_north.png')
}
export const ANGLE_HEAD = {
    name: '艏向',
    value: '艏向',
    icon: require('../../../static/icon/map_orientation.png')
}
export const DEPTH_VISIBLE = false

export default {
    namespaced: true,
    state: {
        landMap: getMaps(LAND_MAP_KEY),
        channelMap: getMaps(CHANNEL_MAP_KEY),
        angle: ANGLE_NORTH.value,
        depth: DEPTH_VISIBLE
    },
    getters: {
        landMap: state => state.landMap,
        channelMap: state => state.channelMap,
        angle: state => state.angle,
        depth: state => state.depth,
    },
    mutations: {
        [SET_LAND_MAP](state, status) {
            state.landMap = status
        },
        [SET_CHANNEL_MAP](state, status) {
            state.channelMap = status
        },
        [SET_ANGLE](state, value) {
            state.angle = value
        },
        [SET_DEPTH](state, visible) {
            state.depth = visible
        },
    },
    actions: {
        setLandMap({commit}, status) {
            commit(SET_LAND_MAP, setMaps(LAND_MAP_KEY, status))
        },
        setChannelMap({commit}, status) {
            commit(SET_CHANNEL_MAP, setMaps(CHANNEL_MAP_KEY, status))
        },
        resetMaps({commit}) {
            commit(SET_LAND_MAP, resetMaps(LAND_MAP_KEY))
            commit(SET_CHANNEL_MAP, resetMaps(CHANNEL_MAP_KEY))
        }
    }
}



