import {mapGetters, mapActions} from 'vuex'
import {NOTICE} from "../../storage/app"

export default {
    data() {
        return {NOTICE}
    },
    computed: {
        ...mapGetters({
            notice: 'notice/notice'
        })
    },
    methods: {
        ...mapActions({
            setNovice: 'notice/setNovice',
            resetNotice: 'notice/resetNotice'
        })
    }
}



