import Storage from 'good-storage'
import {insertArray, deleteFromArray} from "../common/utils/history"

const VALIDATES_KEY = '__GDHDT_VALIDATE_LIST__'

export function getValidate(phone, type) {
    const validates = Storage.get(VALIDATES_KEY, [])

    const result = validates.find(item => {
        return item.phone === phone && item.type === type
    })

    return result || {}
}

export function saveValidate(validate) {
    const validates = Storage.get(VALIDATES_KEY, [])

    insertArray(validates, validate, item => {
        return item.phone === validate.phone && item.type === validate.type
    })

    Storage.set(VALIDATES_KEY, validates)

    return validate
}

export function deleteValidate(validate) {
    const validates = Storage.get(VALIDATES_KEY, [])

    deleteFromArray(validates, item => {
        return item.phone === validate.phone && item.type === validate.type
    })

    Storage.set(VALIDATES_KEY, validates)
}



