class Dispense {
    on(name, cb) {
        if (!this.handles) {
            Object.defineProperty(this, 'handles', {
                value: {},
                enumerable: false,
                configurable: true,
                writable: true
            })
        }

        if (!this.handles[name]) {
            this.handles[name] = []
        }

        this.handles[name].push(cb)
    }

    emit(name, ...arg) {
        if (this.handles[name]) {
            for (let i = 0; i < this.handles[name].length; i++) {
                this.handles[name][i](...arg)
            }
        }
    }
}

export default new Dispense()



