import {mapGetters, mapMutations, mapActions} from 'vuex'
import {THEME_LIGHT, THEME_NIGHT, SPEECH_GENDER_MAN, SPEECH_GENDER_WOMAN} from "../../storage/app"
import {range, formatDate} from "../utils/time"

let timer = null
let modes = null

export default {
    data() {
        return {
            THEME_LIGHT,
            THEME_NIGHT,
            SPEECH_GENDER_MAN,
            SPEECH_GENDER_WOMAN
        }
    },
    computed: {
        themeIcon() {
            switch (this.theme) {
                case THEME_LIGHT.value:
                    return THEME_LIGHT.icon
                case THEME_NIGHT.value:
                    return THEME_NIGHT.icon
            }
        },
        ...mapGetters({
            network: 'app/network',
            gps: 'app/gps',
            speech: 'app/speech',
            speechGender: 'app/speechGender',
            text: 'app/text',
            brightness: 'app/brightness',
            mode: 'app/mode',
            modeSn: 'app/modeSn',
            modeEn: 'app/modeEn',
            theme: 'app/theme',
            version: 'app/version',
            screen: 'app/screen',
            icons: 'app/icons',
            phones: 'app/phones',
            drawer: 'app/drawer'
        })
    },
    methods: {
        setModeTiming() {
            console.log(`%c设置定时器 ......`, 'color: #ff9800')

            clearTimeout(timer)

            modes = this.theme

            let isRange = range(this.modeSn, this.modeEn)

            if (isRange.status) {
                if (modes !== this.mode) {
                    this.setTheme(this.mode)
                }

                timer = setTimeout(time => {
                    if (modes !== this.mode) {
                        this.setTheme(this.mode)
                    }

                    this.setModeTiming()
                }, isRange.ending)
            } else {
                timer = setTimeout(time => {
                    if (this.mode !== this.theme) {
                        this.setTheme(this.mode)
                    }

                    this.setModeTiming()
                }, isRange.start)
            }


            const consoleText = isRange.status ? '在时间范围内' : '在时间范围外'

            console.log(`%c下次更换主题时间： ${formatDate(isRange.b)}, 状态: ${consoleText}`, 'color: #ff9800')
            console.log(`%c距离更换主题还有： ${isRange.ending}ms ......`, 'color: #ff9800')
        },
        ...mapMutations({
            setNetwork: 'app/SET_NETWORK',
            setGPS: 'app/SET_GPS',
            setVersion: 'app/SET_VERSION',
            setDrawer: 'app/SET_DRAWER'
        }),
        ...mapActions({
            setSpeech: 'app/setSpeech',
            resetSpeech: 'app/resetSpeech',
            setText: 'app/setText',
            resetText: 'app/resetText',
            setBrightness: 'app/setBrightness',
            setMode: 'app/setMode',
            setModeSn: 'app/setModeSn',
            setModeEn: 'app/setModeEn',
            resetMode: 'app/resetMode',
            resetModeSn: 'app/resetModeSn',
            resetModeEn: 'app/resetModeEn',
            resetBrightness: 'app/resetBrightness',
            setTheme: 'app/setTheme',
            resetTheme: 'app/resetTheme',
            setSpeechGender: 'app/setSpeechGender',
            resetSpeechGender: 'app/resetSpeechGender',
            setScreen: 'app/setScreen',
            resetScreen: 'app/resetScreen',
            setIcons: 'app/setIcons',
            clearIcons: 'app/clearIcons',
            setPhones: 'app/setPhones',
            clearPhones: 'app/clearPhones'
        })
    }
}



