import {
    getSpeech,
    setSpeech,
    resetSpeech,
    getText,
    setText,
    resetText,
    getBrightness,
    setBrightness,
    resetBrightness,
    getTheme,
    setTheme,
    resetTheme,
    getGender,
    setGender,
    resetGender,
    getMode,
    setMode,
    resetMode,
    getModeSn,
    getModeEn,
    setModeSn,
    setModeEn,
    resetModeSn,
    resetModeEn,
    SPEECH_GENDER_MAN
} from "../../storage/app"
import {getListHistory, setListHistory, clearListHistory} from "../../storage/history"

const SET_NETWORK = 'SET_NETWORK'
const SET_GPS = 'SET_GPS'
const SET_SPEECH = 'SET_SPEECH'
const SET_SPEECH_GENDER = 'SET_SPEECH_GENDER'
const SET_TEXT = 'SET_TEXT'
const SET_BRIGHTNESS = 'SET_BRIGHTNESS'
const SET_THEME = 'SET_THEME'
const SET_VERSION = 'SET_VERSION'
const SET_WIDTH = 'SET_WIDTH'
const SET_HEIGHT = 'SET_HEIGHT'
const ICONS_KEY = '__GDHDT_ICONS__'
const SET_ICONS = 'SET_ICONS'
const PHONES_KEY = '__GDHDT_PHONES__'
const SET_PHONES = 'SET_PHONES'
const SET_DRAWER = 'SET_DRAWER'
const SET_RAISE = 'SET_RAISE'

// modes
const SET_MODE = 'SET_MODE'
const SET_TIME_SN = 'SET_TIME_SN'
const SET_TIME_EN = 'SET_TIME_EN'

export default {
    namespaced: true,
    state: {
        icons: getListHistory(ICONS_KEY),
        phones: getListHistory(PHONES_KEY),
        network: false,
        gps: false,
        speech: getSpeech(),
        text: getText(),
        mode: getMode(),
        modeSn: getModeSn(),
        modeEn: getModeEn(),
        brightness: getBrightness(),
        theme: getTheme(),
        speechGender: getGender(),
        version: '0.0.0',
        width: '100%',
        height: '100%',
        drawer: false,
        raise: {
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
        }
    },
    getters: {
        network: state => state.network,
        gps: state => state.gps,
        speech: state => state.speech,
        speechGender: state => state.speechGender,
        text: state => state.message,
        mode: state => state.mode,
        modeSn: state => state.modeSn,
        modeEn: state => state.modeEn,
        brightness: state => state.brightness,
        theme: state => state.theme,
        version: state => state.version,
        screen: state => ({width: state.width, height: state.height}),
        icons: state => state.icons,
        phones: state => state.phones,
        drawer: state => state.drawer,
        raise: state => state.raise
    },
    mutations: {
        [SET_NETWORK](state, status) {
            state.network = status
        },
        [SET_GPS](state, status) {
            state.gps = status
        },
        [SET_SPEECH](state, status) {
            state.speech = status
        },
        [SET_TEXT](state, status) {
            state.text = status
        },
        [SET_MODE](state, mode) {
            state.mode = mode
        },
        [SET_TIME_SN](state, time) {
            state.modeSn = time
        },
        [SET_TIME_EN](state, time) {
            state.modeEn = time
        },
        [SET_BRIGHTNESS](state, value) {
            state.brightness = value
        },
        [SET_THEME](state, theme) {
            state.theme = theme
        },
        [SET_SPEECH_GENDER](state, value) {
            state.speechGender = value
        },
        [SET_VERSION](state, version) {
            state.version = version
        },
        [SET_WIDTH](state, width) {
            state.width = width
        },
        [SET_HEIGHT](state, height) {
            state.height = height
        },
        [SET_ICONS](state, list) {
            state.icons = list
        },
        [SET_PHONES](state, list) {
            state.phones = list
        },
        [SET_DRAWER](state, visible) {
            state.drawer = visible
        },
        [SET_RAISE](state, {top, right, bottom, left}){
            if(typeof top === 'number') state.raise.top = top
            if(typeof right === 'number') state.raise.right = right
            if(typeof bottom === 'number') state.raise.bottom = bottom
            if(typeof left === 'number') state.raise.left = left
        }
    },
    actions: {
        // 图标
        setIcons({commit}, list) {
            commit(SET_ICONS, setListHistory(ICONS_KEY, list))
        },
        clearIcons({commit}) {
            commit(SET_ICONS, clearListHistory(ICONS_KEY))
        },
        setPhones({commit}, list) {
            commit(SET_PHONES, setListHistory(PHONES_KEY, list))
        },
        clearPhones({commit}) {
            commit(SET_PHONES, clearListHistory(PHONES_KEY))
        },
        // 导航语音
        setSpeech({commit}, status) {
            commit(SET_SPEECH, setSpeech(status))
        },
        resetSpeech({commit}) {
            commit(SET_SPEECH, resetSpeech())
        },
        // 导航文字
        setText({commit}, status) {
            commit(SET_TEXT, setText(status))
        },
        resetText({commit}) {
            commit(SET_TEXT, resetText())
        },
        // 模式
        setMode({commit}, mode) {
            commit(SET_MODE, setMode(mode))
        },
        setModeSn({commit}, time) {
            commit(SET_TIME_SN, setModeSn(time))
        },
        setModeEn({commit}, time) {
            commit(SET_TIME_EN, setModeEn(time))
        },
        resetMode({commit}) {
            commit(SET_MODE, resetMode())
        },
        resetModeSn({commit}) {
            commit(SET_TIME_SN, resetModeSn())
        },
        resetModeEn({commit}) {
            commit(SET_TIME_EN, resetModeEn())
        },
        // 修改亮度
        setBrightness({commit}, value) {
            commit(SET_BRIGHTNESS, setBrightness(value))
        },
        resetBrightness({commit}) {
            commit(SET_BRIGHTNESS, resetBrightness())
        },
        // 修改主题
        setTheme({commit}, theme) {
            if (window.Arcgis) {
                window.Arcgis.changeTheme(theme, () => {
                    commit(SET_THEME, setTheme(theme))
                })
            } else {
                commit(SET_THEME, setTheme(theme))
            }
        },
        resetTheme({commit}) {
            const theme = resetTheme()

            window.Arcgis && window.Arcgis.changeTheme(theme, () => {
                commit(SET_THEME, theme)
            })
        },
        // 导航语音性别
        setSpeechGender({commit}, value) {
            window.Arcgis && window.Arcgis.setSpeaker(value, () => {
                commit(SET_SPEECH_GENDER, setGender(value))
            })
        },
        resetSpeechGender({commit}) {
            window.Arcgis && window.Arcgis.setSpeaker(SPEECH_GENDER_MAN.value, () => {
                commit(SET_SPEECH_GENDER, resetGender())
            })
        },
        // 修改 APP 宽高
        setScreen({commit}, {width, height}) {
            commit(SET_WIDTH, width)
            commit(SET_HEIGHT, height)
        },
        resetScreen({commit}) {
            commit(SET_WIDTH, '100%')
            commit(SET_HEIGHT, '100%')
        }
    }
}



