export const minute = 60
export const hour = minute * 60
export const day = hour * 24
export const month = day * 30

export function formatDate(time, format = 'YYYY-MM-DD HH:mm:ss ddd') {
    if (typeof time !== 'number' && typeof time !== 'string' && !(time instanceof Date) || time === '') return ''

    const week = ["\u65e5", "\u4e00", "\u4e8c", "\u4e09", "\u56db", "\u4e94", "\u516d"]


    if (typeof time === 'string') {
        time = time.replace(new RegExp(/-/gm), "/")
    }

    time = new Date(time)
    const Y = time.getFullYear()
    const M = time.getMonth() + 1
    const D = time.getDate()
    const d = time.getDay()
    const H = time.getHours()
    const m = time.getMinutes()
    const s = time.getSeconds()

    return format.replace(/Y{1,4}|M{1,2}|D{1,2}|H{1,2}|m{1,2}|s{1,2}|d{1,3}/g, function (str) {
        switch (str) {
            case 'Y' :
                return Y
            case 'YY':
                return String(Y).substr(2, 4)
            case 'YYY':
                return Y
            case 'YYYY':
                return Y
            case 'M':
                return M
            case 'MM':
                return M < 10 ? '0' + M : M
            case 'D':
                return D
            case 'DD':
                return D < 10 ? '0' + D : D
            case 'H':
                return H
            case 'HH':
                return H < 10 ? '0' + H : H
            case 'm':
                return m
            case 'mm':
                return m < 10 ? '0' + m : m
            case 's':
                return s
            case 'ss' :
                return s < 10 ? '0' + s : s
            case 'd':
                return '\u5468' + week[d]
            case 'dd':
                return '\u5468' + week[d]
            case 'ddd':
                return '\u661f\u671f' + week[d]
        }
    })
}

export function formatPast(time, format = 'YYYY-MM-DD') {
    if (typeof time !== 'number' && typeof time !== 'string' && !(time instanceof Date) || time === '') return ''
    const currentTime = new Date()
    const past = new Date(time)

    const milliseconds = Math.round((currentTime - past) / 1000)

    if (milliseconds < 0) {
        return ''
    }

    if (milliseconds <= minute) {
        return '刚刚'
    } else if (minute < milliseconds && milliseconds <= hour) {
        return Math.round(milliseconds / minute) + '分钟前'
    } else if (hour < milliseconds && milliseconds <= day) {
        return Math.round(milliseconds / hour) + '小时前'
    }
    // else if (day < milliseconds && milliseconds <= month) {
    //     return Math.round(milliseconds / day) + '天前'
    // }
    // else if (milliseconds > month && currentTime.getFullYear() === past.getFullYear()) {
    //     return format.formatDate(time, 'MM-DD')
    // }
    else {
        return formatDate(time, format)
    }
}

export function formatWalk(time) {
    if (typeof time !== 'number' && typeof time !== 'string' && !(time instanceof Date) || time === '') return ''

    const currentTime = new Date()

    const today = new Date(currentTime.getFullYear(), currentTime.getMonth(), currentTime.getDate())

    return currentTime - today
}

export function range(begin, end, c = new Date()) {
    let b = new Date(), e = new Date()

    begin = begin >= day ? begin % (day * 1000) : begin
    end = end >= day ? end % (day * 1000) : end

    if (begin - end > 0) {
        e.setDate(e.getDate() + 1)
    }

    b.setHours(new Date(begin).getHours())
    e.setHours(new Date(end).getHours())
    b.setMinutes(new Date(begin).getMinutes())
    e.setMinutes(new Date(end).getMinutes())

    if (c >= b && c <= e) {
        return {
            status: c >= b && c <= e,
            start: b - c,
            ending: e - c,
            b,
            e
        }
    } else {
        b.setDate(b.getDate() + 1)
        e.setDate(e.getDate() + 1)

        return {
            status: c >= b && c <= e,
            start: b - c,
            ending: e - c,
            b,
            e
        }
    }
}

export function secondToDate(time, format = '', forbidden) {
    if (typeof time !== 'number' && typeof time !== 'string' && !(time instanceof Date) || time === '' || time === Infinity) time = 0

    if (forbidden) {
        time = Math.ceil(time / 60) * 60
    }

    let M = Math.floor(time / month)
    let D = Math.floor(time / day)
    let H = Math.floor(time / hour)
    let m = Math.floor(time / minute % minute)
    let s = Math.floor(time % 60)

    if (!format) {
        if (M) {
            format += 'MM月'
        }
        if (D) {
            format += 'DD天'
        }
        if (H) {
            format += 'HH小时'
        }

        if (forbidden) {
            format += 'mm分钟'
        } else {
            if (m) {
                format += 'mm分'
            }

            format += 'ss秒'
        }
    }

    return format.replace(/M{1,2}|D{1,2}|H{1,2}|m{1,2}|s{1,2}/g, function (str) {
        switch (str) {
            case 'M':
                return M
            case 'MM':
                return M < 10 ? '0' + M : M
            case 'D':
                return D
            case 'DD':
                return D < 10 ? '0' + D : D
            case 'H':
                return H
            case 'HH':
                return H < 10 ? '0' + H : H
            case 'm':
                return m
            case 'mm':
                return m < 10 ? '0' + m : m
            case 's':
                return s
            case 'ss' :
                return s < 10 ? '0' + s : s
        }
    })
}

export default {
    formatDate,
    formatPast,
    formatWalk,
    range,
    secondToDate
}



