import {getAgree, getNovice, resetAgree, resetNovice, setAgree, setNovice} from "../../storage/novice"

const SET_NOVICE = 'SET_NOVICE'
const SET_AGREE = 'SET_AGREE'

export default {
    namespaced: true,
    state: {
        novice: getNovice(),
        agree: getAgree()
    },
    getters: {
        novice: state => state.novice,
        agree: state => state.agree
    },
    mutations: {
        [SET_NOVICE](state, value) {
            state.novice = value
        },
        [SET_AGREE](state, value) {
            state.agree = value
        }
    },
    actions: {
        nextStep({commit, state}) {
            const step = state.novice + 1

            commit(SET_NOVICE, setNovice(step))
        },
        resetNovice({commit}) {
            commit(SET_NOVICE, resetNovice())
        },
        readAgreeEnd({commit}) {
            commit(SET_AGREE, setAgree(true))
        },
        resetAgree({commit}) {
            commit(SET_AGREE, resetAgree())
        }
    }
}



