import axios, {json} from '../common/js/axios'

/***
 * 文件上传
 * @param file
 * @return {AxiosPromise<any>}
 */
export function upload(file) {
    const url = `/sys/file/upload`

    return axios.post(url, {file})
}

/***
 * 文件上传
 * @param base64
 * @return {AxiosPromise<any>}
 */
export function uploadBase64(base64) {
    const url = `/sys/file/uploadBase64`

    return axios.post(url, {base64})
}

/***
 * 获取离线地图下载列表
 * @return {AxiosPromise<any>}
 */
export function downloadMapList(params) {
    const url = `/sys/download/map/getlist`

    return axios.get(url, {params})
}

/***
 * 获取离线地图文件下载连接
 * @param id
 * @return {AxiosPromise<any>}
 */
export function downloadMapUrl(id) {
    const url = `/sys/download/map/geturl`

    return axios.get(url, {params: {id}})
}

/***
 * 下载地图文件
 * @param url   // 下载地址
 * @param update   // 更新回调
 * @return {AxiosPromise<any>}
 */
export function downloadMap(url, update) {
    return axios.get(url, {}, {
        onDownloadProgress(event) {
            typeof update === 'function' && update(event)
        },
        responseType: 'blob'
    })
}

/**
 * 检查更新
 * @param version
 */
export function checkUpdate(version) {
    const url = `/sys/download/sys/checkupdate`

    return axios.get(url, {params: {version}})
}

/***
 * 获取配置
 * @param params
 * @param params.key
 * @return {*}
 */
export function getSetters(params) {
    const url = `/sys/settings`

    return axios.get(url, {params}).then(res => res.map(item => {
        const obj = {};
        const arr = typeof item.value === 'string' ? item.value.split(';') : [item.value]

        obj.id = item.id
        obj.typeId = item.keyWord

        if (arr.length === 1) {
            obj.value = item.value
        }

        if (arr.length === 2) {
            obj.value = arr[0]
            obj.color = arr[1]
        }

        if (arr.length === 3) {
            obj.value = arr[0]
            obj.icon = arr[1]
            obj.color = arr[2]
        }

        return obj
    }))
}

/***
 * 日志上报
 * @param type
 * @param data
 * @return {Promise|AxiosPromise<any>|void}
 */
export function submitLogger(type, data) {
    let url
    switch (type) {
        case 'error':
            url = `/public/logger/app`
            break
        case 'route':
            url = `/public/logger/route`
    }

    if (!url) return Promise.reject(`Parameter 'type' error, not corresponding to URL. Error: ${type}`)

    return json.post(url, data)
}



