<template>
    <svg class="icon-svg" :class="value" aria-hidden="true">
        <use :xlink:href="svg"></use>
    </svg>
</template>

<script>
    export default {
        name: "icon-svg",
        props: {
            value: {
                type: String,
                default: '',
                required: true
            }
        },
        computed: {
            svg() {
                return `#icon-${this.value}`
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .icon-svg
        padding 3px
        width 1em
        height 1em
        vertical-align -0.15em
        fill currentColor
        overflow hidden
</style>



