const SET_ELEMENT = 'SET_ELEMENT'


export default {
    namespaced: true,
    state: {
        elementData: {}
    },
    getters: {
        elementData: state => state.elementData
    },
    mutations: {
        [SET_ELEMENT](state, data) {
            state.elementData = data
        }
    },
    actions: {}
}



