import {mapGetters, mapMutations, mapActions} from 'vuex'

export default {
    computed: {
        ...mapGetters({
            en: 'route/en',
            sn: 'route/sn',
            history: 'route/history',
            searchHistory: 'route/searchHistory',
            sail: 'route/sail'
        })
    },
    methods: {
        ...mapMutations({
            setRouteSn: 'route/SET_ROUTE_SN',
            setRouteEn: 'route/SET_ROUTE_EN'
        }),
        ...mapActions({
            resetRoutes: 'route/resetRoutes',
            reverseRoutes: 'route/reverseRoutes',
            saveRouteHistory: 'route/saveRouteHistory',
            deleteRouteHistory: 'route/deleteRouteHistory',
            clearRouteHistory: 'route/clearRouteHistory',
            saveRouteSearchHistory : 'route/saveSearchHistory',
            deleteRouteSearchHistory : 'route/deleteSearchHistory',
            clearRouteSearchHistory : 'route/clearSearchHistory'
        })
    }
}



