import {mapGetters, mapActions} from 'vuex'
import {LAYERS} from "../../storage/layer"

export default {
    data() {
        return {LAYERS}
    },
    computed: {
        ...mapGetters({
            layers: 'layer/layers'
        }),
    },
    methods: {
        getLayer(id) {
            return this.layers.find(item => {
                return item.id === id
            })
        },
        ...mapActions({
            setLayers: 'layer/setLayers',
            saveLayers: 'layer/saveLayers',
            resetLayers: 'layer/resetLayers'
        })
    }
}



