export function setMapCenterAt(x, y, scale) {
    return new Promise((resolve, reject) => {
        if (!window.Arcgis || typeof window.Arcgis.centerAt !== 'function') {
            return reject(tips.map.set_center)
        }

        if (!x || !y) {
            return reject(tips.map.set_center_error)
        }

        window.Arcgis.centerAt(x, y, resolve, reject, scale)
    })
}

