import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import types from './types/index'
import TransferDom from './common/utils/transfer-dom'

// Components
import MuseUI from 'muse-ui'
import Loading from 'muse-ui-loading'
import Message from 'muse-ui-message'
import Field from 'vant/lib/field'
import Toast from 'vant/lib/toast'
import VueLazyload from 'vue-lazyload'
// Storage
import {getResources} from "./storage/app"

// Css
import 'vant/lib/toast/style'
import 'muse-ui/dist/muse-ui.css'
import 'muse-ui-loading/dist/muse-ui-loading.css'
import 'muse-ui-message/dist/muse-ui-message.css'
import './common/stylus/index.styl'
import 'vant/lib/field/style'

// icons svg.
import './base/icon-svg/index'

// theme extend init.
types.theme.init()

// Config
Vue.config.productionTip = false

Toast.setDefaultOptions({
    position: 'bottom',
    zIndex: 10000
})


// window
window.$depth = 3
window.$types = types

// Vue use
Vue.use(MuseUI).use(Loading).use(Message).use(Toast)
Vue.component('field', Field)
Vue.directive('transfer-dom', TransferDom)
Vue.filter('formatDistance', function (value, decimal = 10) {
    if (!value) return 0

    let num = Number.parseFloat(value)

    value = Math.round((num > 1000 ? num / 1000 : num) * decimal) / decimal

    return value
})
Vue.filter('formatFind', function (value, arr = [], name, target) {
    if (!value || !Array.isArray(arr)) {
        return ''
    }

    const result = arr.find(item => {
        if (target) {
            return item[target] === value
        } else {
            return item === value
        }
    })


    if (target) {
        return result ? result[name] : ''
    } else {
        return result
    }
})

Vue.prototype.$images = getResources()


// or with options
Vue.use(VueLazyload, {
    preLoad: 1.3,
    error: 'static/image/error.png',
    loading: 'static/image/loading.gif',
    attempt: 1
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
})



