import {getUser, setUser, removeUser} from "../../storage/user"

const SET_USER = 'SET_USER'

export default {
    namespaced: true,
    state: {
        user: getUser()
    },
    getters: {
        user: state => state.user,
        token: state => state.user.pwd,
        isLogin: state => state.user.pwd && state.user.id
    },
    mutations: {
        [SET_USER](state, information) {
            state.user = information
        }
    },
    actions: {
        login({commit}, information) {
            commit(SET_USER, setUser(information))
        },
        logout({commit}) {
            commit(SET_USER, removeUser())
        }
    }
}



