import {mapGetters, mapActions} from 'vuex'

export default {
    computed: {
        ...mapGetters({
            history: 'search/history',
            searchIcons: 'search/icons',
            searchList: 'search/list'
        })
    },
    methods: {
        ...mapActions({
            saveSearchHistory: 'search/saveSearchHistory',
            deleteSearchHistory: 'search/deleteSearchHistory',
            clearSearchHistory: 'search/clearSearchHistory',
            saveSearchIcons: 'search/saveSearchIcons',
            clearSearchIcons: 'search/clearSearchIcons',
            saveSearchList: 'search/saveSearchList',
            clearSearchList: 'search/clearSearchList',
        })
    }
}



