import {
    getListHistory,
    setListHistory,
    clearListHistory
} from "../../storage/history"

const SET_NAVIGATIONAL = 'SET_NAVIGATIONAL'
const NAVIGATIONAL_KEY = '__GDHDT_NAVIGATIONAL_ICONS__'

export default {
    namespaced: true,
    state: {
        icons: getListHistory(NAVIGATIONAL_KEY)
    },
    getters: {
        icons: state => state.icons
    },
    mutations: {
        [SET_NAVIGATIONAL](state, list) {
            state.navigational = list
        }
    },
    actions: {
        setNavigationalIcons({commit}, list) {
            commit(SET_NAVIGATIONAL, setListHistory(NAVIGATIONAL_KEY, list))
        },
        clearNavigationalIcons({commit}) {
            commit(SET_NAVIGATIONAL, clearListHistory(NAVIGATIONAL_KEY))
        }
    }
}



