import {getHistory, setHistory, removeHistory, clearHistory} from "../../storage/history"

const SET_ROUTE_SN = 'SET_ROUTE_SN'
const SET_ROUTE_EN = 'SET_ROUTE_EN'
const SET_ROUTES = 'SET_ROUTES'
const SET_SEARCH = 'SET_ROUTES_SEARCH'
const ROUTES_KEY = '__GDHDT_ROUTES_HISTORY__'
const SEARCH_KEY = '__GDHDT_ROUTES_SEARCH_HISTORY__'
const ROUTES_MAX = 15
const SEARCH_MAX = 20

const STATE_DEFAULT = {
    name: '我的位置',
    p: '起始位置'
}

const SN_DEFAULT = {
    p: '起始位置'
}

const EN_DEFAULT = {
    p: '结束位置'
}

export default {
    namespaced: true,
    state: {
        sn: STATE_DEFAULT,
        en: EN_DEFAULT,
        history: getHistory(ROUTES_KEY),
        searchHistory: getHistory(SEARCH_KEY)
    },
    getters: {
        sn: state => state.sn,
        en: state => state.en,
        history: state => state.history,
        searchHistory: state => state.searchHistory,
        sail: state => state.sn.name && state.en.name
    },
    mutations: {
        [SET_ROUTE_SN](state, route) {
            state.sn = Object.assign({}, SN_DEFAULT, route)
        },
        [SET_ROUTE_EN](state, route) {
            state.en = Object.assign({}, EN_DEFAULT, route)
        },
        [SET_ROUTES](state, routes) {
            state.history = routes
        },
        [SET_SEARCH](state, searchHistory) {
            state.searchHistory = searchHistory
        }
    },
    actions: {
        resetRoutes({commit}) {
            commit(SET_ROUTE_SN, {name: ''})
            commit(SET_ROUTE_EN, {name: ''})
        },
        reverseRoutes({commit, state}) {
            commit(SET_ROUTE_SN, Object.assign({}, state.en, SN_DEFAULT))
            commit(SET_ROUTE_EN, Object.assign({}, state.sn, EN_DEFAULT))
        },
        saveRouteHistory({commit}, route) {
            commit(SET_ROUTES, setHistory(ROUTES_KEY, route, ROUTES_MAX))
        },
        deleteRouteHistory({commit}, route) {
            commit(SET_ROUTES, removeHistory(ROUTES_KEY, route, ROUTES_MAX))
        },
        clearRouteHistory({commit}) {
            commit(SET_ROUTES, clearHistory(ROUTES_KEY))
        },
        saveSearchHistory({commit}, searchHistory) {
            commit(SET_SEARCH, setHistory(SEARCH_KEY, searchHistory, SEARCH_MAX))
        },
        deleteSearchHistory({commit}, searchHistory) {
            commit(SET_SEARCH, removeHistory(SEARCH_KEY, searchHistory, SEARCH_MAX))
        },
        clearSearchHistory({commit}) {
            commit(SET_SEARCH, clearHistory(SEARCH_KEY))
        }
    }
}



