import {mapGetters, mapMutations, mapActions} from 'vuex'
import {ANGLE_HEAD, ANGLE_NORTH} from "../../store/modules/map"
import {LAND_MAP, CHANNEL_MAP} from "../../storage/map"

export default {
    data() {
        return {
            ANGLE_NORTH,
            ANGLE_HEAD,
            LAND_MAP,
            CHANNEL_MAP,
        }
    },
    computed: {
        angleIcon() {
            switch (this.angle) {
                case ANGLE_NORTH.value:
                    return ANGLE_NORTH.icon
                case ANGLE_HEAD.value:
                    return ANGLE_HEAD.icon
            }
        },
        ...mapGetters({
            landMap: 'map/landMap',
            channelMap: 'map/channelMap',
            angle: 'map/angle',
            depth: 'map/depth',
        })
    },
    methods: {
        ...mapMutations({
            setAngle: 'map/SET_ANGLE',
            setDepth: 'map/SET_DEPTH',
        }),
        ...mapActions({
            setLandMap: 'map/setLandMap',
            setChannelMap: 'map/setChannelMap',
            resetMaps: 'map/resetMaps',
        })
    }
}



