import axios from 'axios'
import Toast from 'vant/lib/toast'
import Store from '../../../store'
import Router from '../../../router'

function logout() {
    Toast('当前登录信息已失效，请重新登录！')

    Store.dispatch('user/logout')

    Router.replace('/')
}

/***
 * TODO: FormData 请求参数格式化
 * @param data
 * @returns {FormData}
 */

const instances = [axios.create({
    baseURL: process.env.BASE_URL,
    headers: {
        post: {
            'Content-Type': 'multipart/form-data'
        }
    },
    transformRequest: [function (data) {
        let formData = new FormData()

        for (let key in data) {
            formData.append(key, data[key])
        }

        return formData
    }]
}), axios.create({
    baseURL: process.env.BASE_URL,
    headers: {
        'Content-Type': 'application/json'
    }
})]

instances.forEach(item => {
    // request interceptors
    item.interceptors.request.use(function (config) {
        // token ...
        if (!config.isTransferToken) {
            const token = Store.getters['user/token']

            if (token) config.headers['token'] = token
        }

        // code ...
        console.log('%c—————— axios request config ——————', 'color: #2196f3')
        console.log(config)
        console.log('%c—————— axios request config ——————', 'color: #2196f3')

        return config
    }, function (error) {

        Toast('网络连接中断')

        // error
        return Promise.reject(error)
    })

    // response interceptors
    item.interceptors.response.use(response => {
        console.log('%c—————— axios response result ——————', 'color: #2196f3')
        console.log(response)
        console.log('%c—————— axios response result ——————', 'color: #2196f3')
        // code ...
        const {data: {code, msg, data}} = response


        if (code < 0) {
            console.log(code)

            if (code === -9) {
                logout()
                return
            }


            Toast(msg)

            return Promise.reject(msg)
        }

        return data
    }, error => {
        if (error.response && error.response.status) {
            switch (error.response.status) {
                case 400:
                    Toast('请求错误')
                    break
                case 401:
                    logout()
                    break
                case 403:
                    Toast('拒绝访问')
                    break
                case 404:
                    Toast(`请求地址出错: ${error.response.config.url}`)
                    break
                case 408:
                    Toast('请求超时')
                    break
                case 500:
                    Toast('服务器内部错误')
                    break
                case 501:
                    Toast('服务未实现')
                    break
                case 502:
                    Toast('网关错误')
                    break
                case 503:
                    Toast('服务不可用')
                    break
                case 504:
                    Toast('网关超时')
                    break
                case 505:
                    Toast('HTTP版本不受支持')
                    break
                default:
                    Toast('请求失败')
            }
        }

        // error
        return Promise.reject(error)
    })
})

export const formdata = instances[0]

export const json = instances[1]

export default formdata



