import {mapGetters, mapActions} from 'vuex'

export default {
    data() {
        return {
            logout_avatar: require('../../../static/icon/map_personal_center_avatar.png'),
            login_avatar: require('../../../static/icon/personal_center_avatar.png')
        }
    },
    computed: {
        ...mapGetters({
            user: 'user/user',
            isLogin: 'user/isLogin'
        })
    },
    methods: {
        AvatarImage(src, id) {
            let error
            if (this.isLogin && (!id || this.user.id === id))
                error = this.login_avatar
            else
                error = this.logout_avatar

            return {
                src,
                error
            }
        },
        ...mapActions({
            login: 'user/login',
            logout: 'user/logout'
        })
    }
}



