import Storage from 'good-storage'
import {insertArray, deleteFromArray} from "../common/utils/history"


export function getHistory(key) {
    return Storage.get(key, [])
}

export function setHistory(key, target, max = 15) {
    let searches = getHistory(key)

    insertArray(searches, target, item => {
        return item === target
    }, max)

    Storage.set(key, searches)

    return searches
}

export function removeHistory(key, target) {
    let searches = getHistory(key)

    deleteFromArray(searches, item => {
        return item === target
    })

    Storage.set(key, searches)

    return searches
}

export function clearHistory(key) {
    Storage.remove(key)

    return []
}

export function getListHistory(key) {
    return Storage.get(key, [])
}

export function setListHistory(key, list) {
    Storage.set(key, list)

    return list
}

export function clearListHistory(key) {
    Storage.set(key, [])

    return []
}



