import {
    getHistory,
    setHistory,
    removeHistory,
    clearHistory,
    getListHistory,
    setListHistory,
    clearListHistory,
} from "../../storage/history"

const SET_SEARCH = 'SET_SEARCH'
const SET_SEARCH_ICONS = 'SET_SEARCH_ICONS'
const SET_SEARCH_LIST = 'SET_SEARCH_LIST'
const SEARCH_KEY = '__GDHDT_SEARCH_HISTORY__'
const SEARCH_ICONS_KEY = '__GDHDT_SEARCH_ICONS_HISTORY__'
const SEARCH_LIST_KEY = '__GDHDT_SEARCH_LIST_HISTORY__'
const SEARCH_MAX = 15


export default {
    namespaced: true,
    state: {
        history: getHistory(SEARCH_KEY),
        icons: getListHistory(SEARCH_ICONS_KEY),
        list: getListHistory(SEARCH_LIST_KEY)
    },
    getters: {
        history: state => state.history,
        icons: state => state.icons,
        list: state => state.list
    },
    mutations: {
        [SET_SEARCH](state, history) {
            state.history = history
        },
        [SET_SEARCH_ICONS](state, list) {
            state.icons = list
        },
        [SET_SEARCH_LIST](state, list) {
            state.list = list
        }
    },
    actions: {
        saveSearchHistory({commit}, history) {
            commit(SET_SEARCH, setHistory(SEARCH_KEY, history, SEARCH_MAX))
        },
        deleteSearchHistory({commit}, history) {
            commit(SET_SEARCH, removeHistory(SEARCH_KEY, history))
        },
        clearSearchHistory({commit}) {
            commit(SET_SEARCH, clearHistory(SEARCH_KEY))
        },
        saveSearchIcons({commit}, list) {
            commit(SET_SEARCH_ICONS, setListHistory(SEARCH_ICONS_KEY, list))
        },
        clearSearchIcons({commit}) {
            commit(SET_SEARCH_ICONS, clearListHistory(SEARCH_ICONS_KEY))
        },
        saveSearchList({commit}, list) {
            commit(SET_SEARCH_LIST, setListHistory(SEARCH_LIST_KEY, list))
        },
        clearSearchList({commit}) {
            commit(SET_SEARCH_LIST, clearListHistory(SEARCH_LIST_KEY))
        }
    }
}



