import {
    getNotice,
    setNotice,
    resetNotice
} from "../../storage/app"

const SET_NOTICE = 'SET_NOTICE'

export default {
    namespaced: true,
    state: {
        notice: getNotice()
    },
    getters: {
        notice: state => state.notice
    },
    mutations: {
        [SET_NOTICE](state, value) {
            state.notice = value
        }
    },
    actions: {
        setNovice({commit}, notice) {
            commit(SET_NOTICE, setNotice(notice))
        },
        resetNotice({commit}) {
            commit(SET_NOTICE, resetNotice())
        }
    }
}



