import {mapGetters, mapMutations, mapActions} from 'vuex'

export default {
    computed: {
        ...mapGetters({
            elementData: 'element/elementData'
        })
    },
    methods: {
        ...mapMutations({
            setElementData: 'element/SET_ELEMENT'
        })
    }
}



