import Storage from 'good-storage'

const NOVICE_KEY = '__GDHDT_NOVICE_KEY__'
const AGREE_KEY = '__GDHDT_AGREE__'

export function getNovice() {
    return Storage.get(NOVICE_KEY, 0)
}

export function setNovice(value) {
    Storage.set(NOVICE_KEY, value)

    return value
}

export function resetNovice() {
    Storage.set(NOVICE_KEY, 0)

    return 0
}

export function getAgree() {
    return Storage.get(AGREE_KEY, false)
}

export function setAgree(value) {
    Storage.set(AGREE_KEY, value)

    return value
}

export function resetAgree() {
    Storage.set(AGREE_KEY, false)

    return false
}



