import theme from 'muse-ui/lib/theme';

export function init() {
    theme.addCreateTheme(theme => {
        console.log('主题文件加载: ',theme)

        let appNavigatorColors = [], aberration = [],
            tipColor,
            themeTextColor,
            themeTextSubColor,
            pickerDisplay

        switch (theme.type) {
            case 'light':
                appNavigatorColors = ['#3899D7', '#288DCD']
                aberration = ['#FFFFFF', '#F9F9F9']
                tipColor = theme.text.primary
                themeTextColor = '#2196f3'
                themeTextSubColor = '#2196f3'
                pickerDisplay = theme.primary
                break;
            case 'dark':
                appNavigatorColors = ['#191B21', '#13151A']
                aberration = [theme.background.paper, theme.background.chip]
                tipColor = theme.text.secondary
                themeTextColor = '#fff'
                themeTextSubColor = theme.text.secondary
                pickerDisplay = '#555555'
                break;
        }

        return `
    
    #about-header:before {
        background-color: ${appNavigatorColors[0]} !important
    }
    
    a {
        color: ${theme.text.primary}
    }
    
    .views {
        background-color: ${theme.background.default} !important
    }
    
    .paper {
        background-color: ${theme.background.paper} !important
    }
    
    .paper-before:before {
        background-color: ${theme.background.paper} !important;
        border-color: ${theme.background.paper} !important
    }
    
    .chip {
        background-color: ${theme.background.chip} !important
    }
    
    .theme-background-default {
        background-color: ${theme.background.default} !important
    }
    
    .theme-text-color {
        color: ${themeTextColor}
    }
    
    .theme-text-sub-color {
        color: ${themeTextSubColor}
    }
    
    .theme-selected {
        color: ${theme.primary}
    }
    
    .theme-text-primary {
        color: ${theme.text.primary} !important
    }
    
    .theme-text-secondary {
        color: ${theme.text.secondary} !important
    }
    
    .delete {
        background-color: ${theme.error} !important
    }
    
    .modify {
        background-color: #3f51b5 !important
    }
    
    .default {
        background-color: #673ab7 !important
    }
    
    .background-primary {
        background-color: ${theme.primary} !important
    }
    
    .background-secondary {
        background-color: ${theme.secondary} !important
    }
    
    .background-success {
        background-color: ${theme.success} !important
    }
    
    .background-warning {
        background-color: ${theme.warning} !important
    }
    
    .background-info {
        background-color: ${theme.info} !important
    }
    
    .background-error {
        background-color: ${theme.error} !important
    }
    
    .background-track {
        background-color: ${theme.track} !important
    }
    
    .color-primary {
        color: ${theme.primary} !important
    }
    
    .color-secondary {
        color: ${theme.secondary} !important
    }
    
    .color-success {
        color: ${theme.success} !important
    }
    
    .color-warning {
        color: ${theme.warning} !important
    }
    
    .color-info {
        color: ${theme.info} !important
    }
    
    .color-error {
        color: ${theme.error} !important
    }
    
    .color-track {
        color: ${theme.track} !important
    }
    
    .tips {
        color: ${tipColor}
    }
    
    .mu-flat-button {
        color: ${theme.text.primary}
    }
    
    .mu-raised-button.disabled {
        color: ${theme.text.disabled};
        background-color: ${theme.text.default}
    }
    
    .mu-flat-button.disabled {
        color: ${theme.text.disabled}
    }
    
    .mu-flat-button.bg.disabled {
        background-color: ${theme.background.chip} !important
    }
    
    .mu-checkbox {
        color: ${theme.text.secondary}
    }
    
    .mu-checkbox-checked {
        color: ${theme.primary}
    }
    
    .mu-form-item {
        color: ${theme.text.secondary}
    }
    
    .mu-select-input {
        color: ${theme.text.primary}
    }
    
    .mu-input__focus {
        color: ${theme.primary}
    }
    
    .mu-picker {
        color: ${theme.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-picker-display {
        background-color: ${pickerDisplay}
    }
    
    .mu-datetime-picker .mu-tabs {
        color: ${theme.text.secondary};
        background-color: ${pickerDisplay}
    }
    
    .mu-datepicker-week, 
    .mu-datepicker-toolbar-title, 
    .mu-datepicker-tool-btn, 
    .mu-datepicker-svg-icon, 
    .mu-day-button-text, 
    .mu-month-button-text, 
    .mu-year-button-text, 
    .mu-timepicker-number {
        color: ${theme.text.primary};
    }
    
    .mu-loading-wrap {
        background-color: ${theme.text.alternate} !important;
        opacity: 0.5
    }
    
    .navigator {
        background-image: linear-gradient(45deg, ${appNavigatorColors[0]} 62%, ${appNavigatorColors[1]} 35%)
    }
    
    .navigator-colors-1 {
        background-color: ${appNavigatorColors[0]} !important
    }
    
    .navigator-colors-2 {
        background-color: ${appNavigatorColors[1]} !important
    }
    
    .aberration-wrapper-color {
        background-color: ${aberration[0]} !important
    }
    
    .aberration-color {
        background-color: ${aberration[1]} !important
    }
    
    .bottom-navigator {
        background-color: ${theme.background.paper} !important
    }
    
    .map-button, .map-stack-item {
        background-color: ${theme.background.paper} !important
    }
    
    .map-scale {
        border-color: ${theme.text.primary} !important
    }
    
    .mu-input {
        color: ${theme.text.secondary};
    }
    
    .mu-text-field-input, .van-field {
        color: ${theme.text.primary} !important;
        background-color: transparent !important
    }
    
    .mu-input-help {
        color: ${theme.text.secondary};
    }
    
    .mu-input-suffix-text, .mu-input-prefix-text {
        color: ${theme.text.secondary};
    }
    
    .mu-item {
        color: ${theme.text.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-list .mu-list {
        background-color: ${theme.background.paper}
    }
    
    .mu-item-sub-title {
        color: ${theme.text.secondary}
    }
    
    .mu-item-after-text {
        color: ${theme.text.secondary}
    }
    
    .mu-item-action {
        color: ${theme.text.secondary}
    }
    
    .mu-bottom-sheet {
        background-color: ${theme.background.paper}
    }
    
    .van-popup {
        color: ${theme.text.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-popover {
        color: ${theme.text.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-drawer {
        color: ${theme.text.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-dialog {
        color: ${theme.text.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-dialog-title {
        color: ${theme.text.primary}
    }
    
    .progress-text {
        color: ${theme.text.alternate}
    }
    
    .mu-dialog-body {
        color: ${theme.text.secondary}
    }
    
    .mu-picker {
        color: ${theme.primary};
        background-color: ${theme.background.paper}
    }
    
    .mu-sub-header {
        color: ${theme.text.secondary}
    }
    
    .mu-timepicker-hour-button, .mu-timepicker-minute-button {
        color: ${theme.text.primary}
    }
    
    .m-switch {
        color: ${theme.text.primary};
        box-shadow: 0 0 0 2px ${theme.primary} inset
    }
    
    .m-switch .m-switch-bar {
        background-color: ${theme.primary}
    }
    
    .m-switch.small {
        color: ${theme.text.primary};
        box-shadow: 0 0 0 1px ${theme.primary} inset
    }
    
    .m-switch.green {
        color: ${theme.text.primary};
        box-shadow: 0 0 0 2px #2bc662 inset
    }
    
    .m-switch.green .m-switch-bar {
        background-color: #2bc662
    }
    
    .m-switch.small.green {
        color: ${theme.text.primary};
        box-shadow: 0 0 0 1px #2bc662 inset
    }
    
    .border-1px:before,
    .border-1px-t:before,
    .border-1px-b:after,
    .border-1px-tb:before,
    .border-1px-tb:after,
    .border-1px-l:before,
    .border-1px-r:after {
        border-color: ${theme.divider} !important
    }
  `
    })
}



