import Store from '../store'
import {submitLogger} from "../api/sys"

// TODO: 错误日志上报
export const logger = {
    error(data) {
        try {
            if (typeof data === 'string') {
                data = JSON.parse(data)
            }
        } catch (e) {
            console.log(e)
        }

        submitLogger('error', data).then(console.log).catch(console.log)
    }
}

// 网络
export const network = {
    toggle(status) {
        Store.commit('app/SET_NETWORK', status)
    }
}

// GPS
export const gps = {
    toggle(status) {
        Store.commit('app/SET_GPS', status)
    }
}

// 语音
export const speech = {}

// 文字
export const text = {}



