import Storage from 'good-storage'

const SPEECH_KEY = '__GDHDT_SPEECH__'
const TEXT_KEY = '__GDHDT_TEXT__'
const BRIGHTNESS_KEY = '__GDHDT_BRIGHTNESS__'
const THEME_KEY = '__GDHDT_THEME__'
const GENDER_KEY = '__GDHDT_SPEECH_GENDER__'
const NOTICE_KEY = '__GDHDT_NOTICE__'
const MODE_KEY = '__GDHDT_MODE__'
const MODE_SN_KEY = '__GDHDT_MODE_SN__'
const MODE_EN_KEY = '__GDHDT_MODE_EN__'

// 资源服务器
const RESOURCES_KEY = '__GDHDT_RESOURCES__'

// 语音性别
export const SPEECH_GENDER_MAN = {name: '男', value: '0'}
export const SPEECH_GENDER_WOMAN = {name: '女', value: '1'}

// 主题
export const THEME_LIGHT = {
    value: 'light',
    name: '白天',
    icon: require('../../static/icon/map_day.png')
} // 白天
// 黑夜
export const THEME_NIGHT = {
    value: 'dark',
    name: '夜晚',
    icon: require('../../static/icon/map_mode.png')
} // 夜晚

// 提醒
export const NOTICE = {
    push: true,
    ring: true,
    shock: true,
    popup: true,
    disturbed: true
}

// 资源服务器地址
export const RESOURCES = 'http://14.23.108.204:10000/fast/'

// 模式&定时时间
export const SN_TIME = 33600
export const EN_TIME = 43212000
export const MODE = THEME_LIGHT.value


export function getResources() {
    return Storage.get(RESOURCES_KEY, RESOURCES)
}

export function setResources(value) {
    Storage.set(RESOURCES_KEY, value)

    return value
}

export function getMode() {
    return Storage.get(MODE_KEY, MODE)
}

export function setMode(mode) {
    Storage.set(MODE_KEY, mode)

    return mode
}

export function resetMode() {
    Storage.set(MODE_KEY, MODE)

    return MODE
}

export function getModeSn() {
    return Storage.get(MODE_SN_KEY, SN_TIME)
}

export function getModeEn() {
    return Storage.get(MODE_EN_KEY, EN_TIME)
}

export function setModeSn(time) {
    Storage.set(MODE_SN_KEY, time)

    return time
}

export function resetModeSn() {
    Storage.set(MODE_SN_KEY, SN_TIME)

    return SN_TIME
}

export function setModeEn(time) {
    Storage.set(MODE_EN_KEY, time)
    return time
}

export function resetModeEn() {
    Storage.set(MODE_EN_KEY, EN_TIME)

    return EN_TIME
}

// 消息
export function getNotice() {
    return Storage.get(NOTICE_KEY, NOTICE)
}

export function setNotice(notice) {
    Storage.set(NOTICE_KEY, notice)

    return notice
}

export function resetNotice() {
    Storage.set(NOTICE_KEY, NOTICE)

    return NOTICE
}

// 语音
export function getSpeech() {
    return Storage.get(SPEECH_KEY, true)
}

export function setSpeech(status) {
    Storage.set(SPEECH_KEY, status)

    return status
}

export function resetSpeech() {
    Storage.set(SPEECH_KEY, true)

    return true
}

// 文字
export function getText() {
    return Storage.get(TEXT_KEY, true)
}

export function setText(status) {
    Storage.set(TEXT_KEY, status)

    return status
}

export function resetText() {
    Storage.set(TEXT_KEY, true)

    return true
}

// 亮度
export function getBrightness() {
    return Storage.get(BRIGHTNESS_KEY, 0)
}

export function setBrightness(value) {
    Storage.set(BRIGHTNESS_KEY, value)

    return value
}

export function resetBrightness() {
    Storage.set(BRIGHTNESS_KEY, 0)

    return 0
}

// 主题
export function getTheme() {
    return Storage.get(THEME_KEY, THEME_LIGHT.value)
}

export function setTheme(theme) {
    Storage.set(THEME_KEY, theme)

    return theme
}

export function resetTheme() {
    Storage.set(THEME_KEY, THEME_LIGHT.value)

    return THEME_LIGHT.value
}

// 语音性别
export function getGender() {
    return Storage.get(GENDER_KEY, SPEECH_GENDER_WOMAN.value)
}

export function setGender(value) {
    Storage.set(GENDER_KEY, value)

    return value
}

export function resetGender() {
    Storage.set(GENDER_KEY, SPEECH_GENDER_WOMAN.value)

    return SPEECH_GENDER_WOMAN.value

}



